/*? Profile */

.profile {
	padding-bottom: 30px;
	margin-right: 30px;
}

.profile__info {
	min-height: 300px;

	border-radius: 20px;
	background-color: #F9FAFB;
}

.main {
	display: grid;
	grid-template-rows: 1fr;
	grid-template-columns: 250px auto 290px;
	grid-template-areas: "info posts sidebar";
	gap: 30px;

	padding: 30px;
}

.main__sidebar {
	border-radius: 16px;
	background-color: var(--color-white);
}

.main__info {
	grid-area: info;
}

.main__posts {
	grid-area: posts;
}

.main__newPost {
	margin-bottom: 30px;
	box-shadow: var(--shadow-1);
	border-radius: 16px;
}

.post__list {
	display: flex;
	flex-direction: column-reverse;
}

.post__item {}

.main__post > *:not(:last-of-type) {
	margin-bottom: 32px;
}

.main__sidebar {
	grid-area: sidebar;
}


.header {
	width: 100%;
	height: 484px;

	margin-bottom: 30px;

	border-radius: 16px;
	overflow: hidden;

	box-shadow: -2px 4px 12px rgba(0, 0, 0, 0.04);

	position: relative;
}

.header__bg {
	width: 100%;
	height: 350px;

	border-radius: 0 0 4px 4px;
	background: url('/public/img/main/profile/header__bg.webp') center / cover no-repeat;

	position: relative;
}

.header__bg-btn {
	font-size: 14px;
	font-weight: 500;
	color: #4e5d78;

	border-radius: 10px;
	background-color: #fff;

	padding: 14px 14px 14px 40px;

	position: absolute;
	right: 30px;
	bottom: 30px;

	box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
}

.header__bg-btn::before {
	content: '';
	display: block;
	width: 16px;
	height: 16px;

	position: absolute;
	top: 50%;
	left: 10px;
	transform: translateY(-50%);

	background-color: #4e5d78;

	mask: url('/public/img/icons/upload-icon.svg') center / contain no-repeat;
}

.header__info-btn {
	font-size: 14px;
	font-weight: 500;
	color: #4e5d78;

	border-radius: 10px;
	background-color: #F6F7F8;

	padding: 14px 20px;

	position: absolute;
	right: 30px;
	bottom: 42px;
}

.header__card {
	position: absolute;
	left: 30px;
	bottom: 32px;
}

.card {}

.card__img {
	display: block;
	width: 158px;
	height: 158px;

	position: relative;

	border-radius: 50%;
	border: 4px solid #fff;

	background: url('/public/img/photos/id0.jpeg') center / cover no-repeat;

	margin-bottom: 16px;
}

.card__upload {
	pointer-events: none;
	opacity: 0;

	width: 30px;
	height: 30px;

	position: absolute;
	right: 4px;
	bottom: 16px;

	border-radius: 50%;
	background-color: var(--color-white);
}

.card__img:hover .card__upload {
	opacity: 1;
	pointer-events: all;
}

.card__btn {
	width: 100%;
	height: 100%;

	border-radius: 50%;
	background-color: var(--color-primary);
	mask: url('/public/img/icons/upload-icon.svg') center / 14px no-repeat;
}

.card__name {
	font-size: 26px;
	font-weight: 700;
	color: #4e5d78;

	margin-bottom: 10px;
}

.card__desc {
	font-size: 16px;
	font-weight: 500;
	color: #959EAE;
}